import React from 'react';

//import LikePart from './like-part'

import PhotoService from '../../../photo-service/photo-service'

import './image-item.css';

export default class ImageItem extends React.Component {
   

    photoServise = new PhotoService();

    state = {
        numberPictures: 5,
        data: null,
    }
    
    
    componentDidMount() {
        this.photoServise.getImages(1,this.state.numberPictures).then((result) => {
            this.setState({
                data: result
            });
        });
    };

    componentDidUpdate(prevState) {
        if(this.state.numberPictures !== prevState.numberPictures){
            this.photoServise.getImages(1,this.state.numberPictures).then((result) => {
                this.setState({
                    data: result
                });
            });
        }
    }

    addImage = () => {
        this.setState(prevState => ({
            numberPictures: prevState.numberPictures + 5
        }));

    };

    

    render(){
        const { data } = this.state;

        const likeAdd =(id) => {
            debugger
            const newData = data.map((item) => {
                if(item.id === id) {
                    return {
                        id: item.id,
                        download_url: item.download_url, 
                        author: item.author,
                        like: !item.like
                    }
                } else {
                    return { ...item }
                }
            });
            this.setState(() => {
                return {
                    data: newData
                }
            });
    
        };

        if( !data ){
            return null;
        }

        const item = data.map(({id, download_url, author, like}) => {
            return(
                <div className="card-image" key={id}>
                    <a href={download_url}>
                        <img className="img-content"  
                            src={download_url} 
                            alt={author}/>
                    </a>
                    <span className="card-title">{ author }</span>
                    <div >
                        <div className="likeContainer">
                            <button className="likeButton"
                                onClick={ () => likeAdd(id) }>LIKE</button>
                            <span>{ Number( like ) }</span>
                        </div>
                        {/* <LikePart likeAdd = {() => this.likeAdd(id) } 
                                  like = { like } /> */}
                    </div>
                </div>
            );
        });

        return(
            <div>
                <div className="img-container">
                    { item }
                </div>
                <div>
                    <button onClick={ this.addImage } 
                            className="add-image-btn">add more images</button>
                </div>
            </div>       
        );
    };
};